import React from 'react'

const LoadingGif = () => <div style={{ width:'100%', margin:'20vh 0vh'}}><img src='/images/load.gif' style={{margin:'20vh, 0px' ,filter:'hue-rotate(180deg)',  width:'100%'}} alt="" /></div>

export default LoadingGif



